import React, {useState} from 'react';
import xlsx from 'xlsx';
import Barcode from 'react-barcode';
import html2canvas from 'html2canvas';
import './App.css';

function App() {

  const [orders, setOrders] = useState([]);
  const [page, setPage] = useState(0);
  const addOrder = (o) => {
    setOrders(a => [...a, o]);
  }

  const onDragEnter = (event) => {
    console.log("파일 감지");
    event.preventDefault();
  }

  const onDragLeave = (event) => {
    console.log("파일 이탈 감지");
    event.preventDefault();
  }

  const onDragOver = (event) => {
    console.log("이건뭐지");
    event.preventDefault();
  }

  const prevPage = () => {
    setPage(page-1);
  }

  const nextPage = () => {
    setPage(page+1);
  }
  
  const onDropXlsx = (event) => {
    console.log("파일 입력 감지");
    const supportedFilesTypes = ['application/vnd.ms-excel', 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'];
    const files = event.dataTransfer.files;
    for (let i=0;i<files.length;i++) {
      console.log("파일 읽음");
      const file = files[i];
      console.log(`${file.type}`);
      // if(supportedFilesTypes.indexOf(file.type) > -1) {
        console.log("파일 종류 통과");
        const reader = new FileReader();
        reader.onload = e => {
          const bstr = e.target.result;
          const wb = xlsx.read(bstr, {type: 'binary'});
          const wsname = wb.SheetNames[0];
          const ws = wb.Sheets[wsname];
          const data = xlsx.utils.sheet_to_json(ws, {header: 1});
          console.log(data);
          for (let i=21;i<data.length;i+=2) {
            if(data[i][0] != '합계'){
              let name = data[i][2];
              if(name.includes(')')) {
                name = name.split(')')[1];
              }
              name = name.replace(/[\{\}\[\]\/?.,;:|\)*~`!^\-+<>@\#$%&\\\=\(\'\"\s]/gi, '');
              const barcode = data[i+1][2];
              const qty = data[i][5];
              const order = {name: name, barcode: barcode, qty: qty};
              for(let j=0;j<qty;j++) {
                addOrder(order);
              }
            }
          }
        }
        reader.readAsBinaryString(file);
      // }
    }
    window.scrollTo(0,0);
    event.preventDefault();
  }

  const onDonwload = async () => {
    window.scrollTo(0,0);
    const canvas = await html2canvas(document.getElementsByClassName('A4')[0]
      , {
        allowTaint: true
        , useCORS: true
        , letterRendering: 1
        , scrollX: 0
        , scrollY: 0
        , scale: 2.0
      })

      const img = canvas.toDataURL('image/jpeg');

      const element = document.createElement("a");
      element.href = img;
      element.download = `orderlist-${page+1}.jpg`;
      document.body.appendChild(element);
      element.click();
  }

  const leftButton = () => {
    if(page > 0)
      return <button className="pageBtn" onClick={prevPage}>&lt;</button>
    else 
      return <button className="pageBtn" disabled onClick={prevPage}>&lt;</button>
  }

  const rightButton = () => {
    if(page < Math.ceil(orders.length/40)-1)
      return <button className="pageBtn" onClick={nextPage}>&gt;</button>
    else 
      return <button className="pageBtn" disabled onClick={nextPage}>&gt;</button>
  }

  return (
    <div className="App"
    onDragEnter={onDragEnter}
    onDragLeave={onDragLeave}
    onDragOver={onDragOver}
    onDrop={onDropXlsx}>
      <div className="topBar">
        {leftButton()}
        <h3 className="page">{page+1}/{Math.ceil(orders.length/40)}</h3>
        {rightButton()}
        <button className="download" onClick={onDonwload}>다운로드</button>
        {/* <button className="print" onClick={print}>프린트</button> */}
      </div>
      <div className="A4">
        {orders.map((e, i) => {
          if(page*40 > i || (page+1)*40 <= i) return;
          return <div className="order">
                  <p className="itemName">{e.name}</p>
                  <Barcode background='#00000000' value={e.barcode}/>
                </div>
        })}
      </div>
    </div>
  );
}

export default App;
